body{
  margin: 0;
  padding: 0;
  font-family: tahoma,Aria, Lucida Sans Unicode;
  background-color: #12161c;
  color: #000;
  /*background: linear-gradient(102deg, rgb(40, 47, 63) 0%, rgb(18, 22, 28) 100%);*/
}
header{
  box-shadow:none !important;
}
.App {
  text-align: center;
  width: 100%;
  height: 100%;
  flex-grow: 1;
  font-family: Aria,Poppins, Lucida Sans Unicode;
  font-size:20px;
  overflow: hidden;
}

.logo{
  max-width: 30px;
  max-height: 30px;
  margin: 0 5px 0 10px;
}

.logo-large{
  max-width: 80px;
  max-height: 80px;
  margin: 0 20px 0 10px;
}

.logo-medium{
  max-width: 50px;
  max-height: 50px;
  margin: 5px 10px 0 0;
}

.logo-x-m{
  max-width: 20px;
  max-height:20px;
  margin: 5px 0 0 0;
}
.logo-small{
  max-width: 10px;
  max-height:10px;
  margin: 0;
}

.icon{
  width: 30px;
  height: 30px;
  margin: 5px 10px 0 5px;
}


.glowing-pink{
  font-family:  ProximaNova, sans-serif;
  text-shadow: rgb(43, 0, 43) 0 0 5px, rgb(204, 0, 204) 0 0 20px, rgb(255, 0, 255) 0 0 10px;
}

.glowing-blue{
  font-family:  ProximaNova, sans-serif;
  text-shadow: rgb(0, 12, 43) 0 0 5px, rgb(0, 51, 204) 0 0 20px, rgb(0, 106, 255) 0 0 10px;
}

.glowing-green{
  font-family:  ProximaNova, sans-serif;
  text-shadow: rgb(5, 43, 0) 0 0 5px, rgb(0, 204, 40) 0 0 20px, rgb(0, 255, 76) 0 0 10px;
}

.glowing-gold{
  font-family:  ProximaNova, sans-serif;
  text-shadow: rgb(178, 130, 26) 0 0 5px, rgb(204, 200, 0) 0 0 20px, rgb(255, 255, 0) 0 0 10px;
}

.glowing-red {
  font-family:  ProximaNova, sans-serif;
  text-shadow: rgb(178, 60, 24) 0 0 5px, rgb(206, 42, 2) 0 0 20px, rgb(255, 84, 0) 0 0 10px;
}

.glowing-silver {
  font-family:  ProximaNova, sans-serif;
  text-shadow: rgb(175, 174, 174) 0 0 5px, rgb(198, 197, 196) 0 0 20px, rgb(252, 252, 252) 0 0 10px;
}

.deposit-card {
  min-height: 380px;
}

.withdraw-card {
  min-height: 380px;
}

.container{
  width:90%;
  vertical-align: middle;
  flex-grow: 1;
  margin: 0;
  padding: 0;
}

.home-card {
  width:100%;
  margin: 20px 0 0 0;
  padding: 0;
}

.dd-card {
  width:100%;
  margin: 0;
  padding: 0;
}

.component-card{
  background-color: #12161c;
  color: #fff;
  padding:0;
  border-radius: 20px;
  margin:10px 20px 20px 20px;
}

.faq-card{
  padding-bottom: 4px;
  border-radius: 20px;
  margin-top: 20px;
}

.cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-bottom: 20px;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.account-card {
  padding: 20px;
}

.input-box div{
  background-color: #f2eff2;
  border-radius: 3px;
}

.input-box fieldset{
  border:solid 1px #d6d4d6 !important;
  border-radius: 3px;
  z-index: 998;

}

.input-box input{
  color:#000 !important;
  border-radius: 10px;
  z-index: 999;
}

.white-border{
  border: 1px solid #fff;
}

.divs-referral-link-box {
  width: 90%;
}

.global-stats-item {
  font-size: 18px;
  color: white;
  margin: 10px 10px 10px 10px;
}
.xxl-size {
  font-size:40px;
}

.xl-size {
  font-size:32px;
}

.l-size {
  font-size:28px;
}

.m-size{
  font-size:23px;
}

.x-m-size{
  font-size:18px;
}

.s-size{
  font-size:12px;
}

.green{
  color:#10bc27;
}

.red{
 color:#bc2312;
}

.blue{
  color:#003a8e;
}

.gold{
  color:#ed8415;
}

.aqua{
  color:#81acea;
}

.purple {
  color: #9330B2;
}

.white{
  color: #fff;
}
.dark{
  color:#514f4f;
}
.grey{
  color:#9a9a9a;
}
.image-profile{
  height:263px;
}
.image-flag {
  width:25px;
  height:25px;
}
.image-large{
  max-width:150px;
  max-height:150px;
}
.image-medium{
  max-width:120px;
  max-height:120px;
}
.image-small{
  max-width:30px;
  max-height:30px;
  margin:5px;
}
.buy-placeholder {
  vertical-align: middle;
  padding:0;
  background-color: #e5e3e3 !important;
  border-radius: 10px;
  /*background-image: linear-gradient(0deg,#929393,#4b4c4c);*/

  /*border:1px solid #fff;*/
  color:#000;
}

.grey-border{
   border:1px solid #d6d8db;
}


.intro-placeholder {
  width: 100%;
}

.intro-placeholder ul{
  text-align: left;
  vertical-align: top;
  font-size:20px;
  padding-inline-start: 0px !important;
}

.intro-placeholder li{
  list-style-type: none;
  margin-bottom: 5px;
}

.item-placeholder {
  vertical-align: middle;
  padding:0;
  background-color: #f2f3f4!important;
  border-radius: 10px;
  color:#000;
}

.title-placeholder {
  border-radius: 10px 10px 0 0;
  padding: 10px 0;
}

.faq-placeholder{
  text-align: left;
  margin:20px 20px 20px 20px;
  font-size:16px;
}
.faq-placeholder .question{
  border-radius: 5px;
  padding:2px 2px 2px 0;
  color:#000000;
}
.faq-placeholder .answer{
  font-size:13px;
  color:#000;
}



.grey-bg{
  background-color:#ededed;
}

.purple-bg{
  background-color: #9330B2;
}

.yellow-bg{
  background-color: #f9ea45;
}

.blue-bg{
  background-color: #044768;
}

.aqua-bg{
  background-color: #81acea;
}

.dark-bg{
  background-color: #000000;
}
.buy-button{
  background-color: #09b523 !important;
}

.buy-button span{
  color:#ffffff;
  font-size:16px;
}

.invest-button{
  background-image: linear-gradient(90deg,#24D2FE,#4FACFE);
}

.invest-button span{
  text-transform:none !important;
  color:#ffffff;
  font-size:16px;
}

.sell-button{
  background-color: #c11c0b !important;
}

.sell-button span {
  color:#ffffff;
  font-size:16px;
}
.sell-button a {
  color:#ffffff;
  text-decoration: none;
}

.withdraw-button,
.investment-button {
  text-transform:none !important;
  background-image: linear-gradient(90deg,#f28202,#f40202);
}

.investment-button {
  margin-bottom: 20px !important;
}

.withdraw-button span,
.investment-button span {
  color: #ffffff;
  font-size:16px;
}
.withdraw-button a,
.investment-button a {
  color:#ffffff;
  text-decoration: none;
}

.gta-button  {
  margin:5px !important;
  background: linear-gradient(102deg, #04b8ef, #4286f4) !important;
}
.gta-button span {
  color:#fff;
  font-size:10px;
  text-transform: none !important;
}
.gta-button a {
  color:#fff;
  text-decoration: none;
  text-transform: none !important;
}

.gta-invest-button  {
  min-width: 120px !important;
  margin:5px !important;
  background: linear-gradient(102deg, #ef6504, #ed3304) !important;
}
.gta-invest-button span {
  color:#fff;
  font-size:10px;
  text-transform: none !important;
}
.gta-invest-button a {
  color:#fff;
  text-decoration: none;
  text-transform: none !important;
}

.contract-button  {
  background-color: #ffd700 !important;
}
.contract-button span {
  color:#000;
  font-size:10px;
}
.contract-button a {
  color:#000;
  text-decoration: none;
}

.announcement {
  padding: 1px 5px;
  border-radius: 10px;
  background-image: linear-gradient(90deg,#f28202,#f40202);
}

.announcement-text {
  font-size: 20px;
  margin: 10px 5px;
}

.social {
  margin:5px;
}

.footer {
  text-align: center;
  padding: 0 0 20px 0;
}

.space {
  margin-top: 20px;
}

#buy-button,
#sell-button,
#reinvest-button,
#withdraw-button,
#invest-button {
  width: 80%;
  margin: 20px 0;
  font-family:  Aria, sans-serif;
  font-size: 25px;
}

#buy-button:disabled,
#sell-button:disabled,
#reinvest-button:disabled,
#withdraw-button:disabled,
#invest-button:disabled {
  color: #313133 !important;
  box-shadow: none !important;
  background-color: #797a7c !important;
}

.buy-token, .sell-token {
  width: 80%;
  margin: 0 0 6px !important;
}

.equal-sign {
  font-size: 14px;
}

.my-account {
  margin: 0;
  border-radius: 10px 10px 0 0;
  padding: 10px 0;
}

.roadmap-item {
  margin: 0;
  border-radius: 10px;
  padding: 10px;
}

.jumbotron {
  margin: 0;
  border-radius: 10px;
  padding: 10px 0 20px;
}

.tool-bar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo-container {
  display: flex;
  align-items: center;
  min-height:40px !important;
}

.align-left {
  /*display: flex;
  align-items: left;
  min-height:20px !important;*/
}
.m-left-50{
  margin: 50px;
}

.site-title {
  min-width: 240px;
  text-align: start;
  margin-left: 5px;
}

.plan-name,
.my-account-title {
  margin-left: 4px;
}

.menu-link {
  color:#c0c1c4 !important;
  text-decoration: none;
  font-size:16px;
}

.content-card{
  text-align: center;
  align-items: center;
  margin:20px;
}

.menu-link:link,
.menu-link:hover,
.menu-link:visited {
  color:#c0c1c4 !important;
  text-decoration: none;
}


.social-button {
  margin: 0;
  padding: 4px !important;
}

.buy-button a{
  color:#ffffff;
  text-decoration: none;
}

.new {
  font-size: 12px;
  color: #fff;
  padding: 2px 4px;
  background-color: #cc0040;
  border-radius: 2px;
}

.donation-message {
  padding: 0 10px;
}

.copy-right {
  font-size: 15px;
}

.deposit-withdraw-card {
  margin-bottom: 15px;
  padding-top: 20px;
}

.footer-logo {
  width: 16px;
}

.contract-menu-button {
  font-size: 16px !important;
  color: #c0c1c4 !important;
}

.middle-children {
  display: flex;
  align-items: center;
  justify-content: center;
}

.balance-number {
  margin-right: 4px;
}

.vcc-balance-number {
  margin: 0 4px;
}

.left-margin {
  margin-left: 4px;
}

.vcc-buy-button {
  width: 90%;
}

.referral-message {
  margin: 0 10px;
}

.wechat-group {
  width: 320px;
}

.wechat-appbar>div {
  padding: 0 10px;
}

@media only screen and (max-width: 1023px) {
  .desktop-app-bar {
    display: none;
  }
}

@media only screen and (min-width: 1024px) {
  .mobile-app-bar {
    display: none;
  }
}

.drawer > div[role=document] {
  background-color: #343a40;
}

.drawer-menu {
  min-width: 240px;
  background-color: #343a40;
}

.drawer-item {
  height: 50px;
}

.mobile-app-bar-right {
  width: 48px;
  height: 48px;
  opacity: 0;
}

.xxl-text{
  font-size:150px;
}

.glowing-pink{
  font-family:  ProximaNova, sans-serif;
  text-shadow: rgb(43, 0, 43) 0 0 5px, rgb(204, 0, 204) 0 0 20px, rgb(255, 0, 255) 0 0 10px;
}

.medium-text{
  font-size: 50px;
}

@media only screen and (max-width: 1023px) {
  #body {
    padding: 0 15px;
  }

  #buy-button,
  #sell-button,
  #reinvest-button,
  #withdraw-button,
  .buy-token,
  .sell-token,
  .divs-referral-link-box {
    width: 90%;
  }

  .my-account {
    margin: 0 0 20px;
  }

  .deposit-card {
    min-height: 250px;
  }

  .withdraw-card {
    min-height: 580px;
    padding-left: 10px;
    padding-right: 10px;
  }
}
